.mds-view-wrap {
  height: 100vh;
  width: 100vw;
  display: flex;
  .mds-menus {
    display: flex;
    flex-direction: column;
    .mds-view-logo {
      height: 50px;
      font-size: 20px;
      line-height: 50px;
      font-weight: 600;
      padding-left: 20px;
      background-color: #1890ff;
    }
    .ant-menu {
      flex: 1;
    }
  }
  .mds-viewer {
    flex: 1;
    padding: 20px;
    overflow-x: hidden;
    .md-title {
      height: 50px;
      font-size: 30px;
      font-weight: 800;
      text-align: center;
    }
  }
}
